.editor-page {
  height: calc(100vh - 60px);
  padding-top: 15px;

  display: flex;
  background-color: var(--background-primary);
  transition: all 0.5s;
}

.editors-top {
  flex-direction: column;
  width: 100%;
  height: calc(100vh - 60px);
  align-items: center;

  .editors-group {
    width: 97.5%;
    height: 50%;
    min-height: 20%;
    max-height: 65%;

    display: flex;
    justify-content: space-between;
    resize: vertical;
    overflow: auto;

    .editor {
      width: 32.5%;
      height: 100%;
    }
  }

  iframe {
    width: 100%;
    height: 50%;
  }
}

.editors-left {
  flex-direction: row;

  .editors-group {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    .editor {
      width: 100%;
      height: 32%;
    }
  }

  iframe {
    width: 50%;
    height: 100%;
  }
}

.editors-right {
  flex-direction: row-reverse;

  .editors-group {
    width: 50%;
    height: 100%;
  }

  iframe {
    width: 50%;
    height: 100%;
  }
}
